@import './parts/reset.scss';
@import './parts/colors.scss';
@import './parts/breakpoints.scss';

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  color: #2a2a2a;
  line-height: 22px;
}

.content {
  padding: 0 15px 15px;
  max-width: 900px;
  margin: 0 auto;
}

.header {
  padding: 5px 10px;
  background-color: #fff;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  text-align: center;
}

.logo {
  width: 180px;
}

.title1 {
  font-size: 19px;
  font-weight: 700;
  text-align: center;
  padding: 40px 0 35px;
}

.title2 {
  font-size: 17px;
  font-weight: 700;
  line-height: 1.47;
  text-align: center;
  padding: 35px 0;

  @include breakpoint(sm) {
    text-align: left;
  }
}

.title3 {
  font-size: 19px;
  font-weight: 500;
}

.title4 {
  font-size: 14px;
  font-weight: 700;
}

.description {
  font-size: 17px;
  line-height: 26px;
  font-weight: 500;
}

.input_wrapper {
  position: relative;
  padding-bottom: 30px;
  min-width: 275px;
  flex-shrink: 0;

  @include breakpoint(sm) {
    margin-right: 20px;
  }
}

.phone_input {
  width: 100%;
  box-sizing: border-box;
  border-radius: 9px;
  box-shadow: 0 6px 6px 0 rgba(0, 136, 255, 0.16);
  border: solid 2px $blue;
  font-size: 23px;
  padding: 12px 35px 12px 22px;

  &:focus {
    outline: none;
  }
}

.green_checkmark {
  position: absolute;
  top: 19px;
  right: 12px;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: $green;
  text-align: center;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 250ms ease;

  &::before {
    content: '';
    display: inline-block;
    width: 6px;
    height: 3px;
    border-bottom: 2px solid #fff;
    border-left: 2px solid #fff;
    transform: rotate(-45deg) translate(1px, -1px);
  }
}

.visible {
  opacity: 1;
}

.checkbox {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
  padding-top: 17px;
  padding-right: 17px;

  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    &:checked ~ .checkmark:after {
      opacity: 1;
    }
  }

  .checkmark {
    position: absolute;
    top: 2px;
    left: 0;
    width: 17px;
    height: 17px;
    border-radius: 3px;
    background-color: $blue;

    &::after {
      content: '';
      position: absolute;
      left: 6px;
      top: 3px;
      width: 3px;
      height: 6px;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
      opacity: 0;
      transition: opacity 250ms ease;
    }
  }
}

.desktop_row {
  display: flex;
  align-items: center;

  @include breakpoint(xs) {
    display: block;
  }
}

.agreement_wrapper {
  display: flex;
  margin-bottom: 40px;

  @include breakpoint(sm) {
    margin-bottom: 25px;
  }
}

.agreement {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.62;
  margin-left: 13px;
  padding-right: 20px;
}

.scroll_box {
  height: 200px;
  box-sizing: border-box;
  padding: 25px;
  border-radius: 16px;
  border: solid 1px #d9d9d9;
  overflow: auto;
  margin-top: 15px;
  color: #404040;
}

.btn_box {
  text-align: center;
  max-width: 400px;
  margin: 40px auto;
}

.btn_submit {
  width: 100%;
  height: 52px;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  background-color: $blue;
  border-radius: 8px;
  border: none;
  outline: none;
}

.contacts {
  text-align: center;
  margin-bottom: 100px;

  @include breakpoint(sm) {
    text-align: left;
  }
}

.grey_title {
  font-size: 15px;
  font-weight: 500;
  color: #888888;
  margin: 12px 0;
}

.link {
  font-size: 15px;
  font-weight: 500;
  color: $blue;
}

.link_wrapper {
  margin-bottom: 10px;
}

.terms_box {
  text-align: center;
  color: $blue;
  padding-bottom: 50px;
}

.terms_link {
  color: $blue;
  text-decoration: underline;
  font-size: 15px;
}

.footer {
  max-width: 900px;
  margin: 0 auto;
}
